import React from 'react';
import './Security.scss';
import LinkBack from './../../../../components/LinkBack/LinkBack';
import { ACCOUNT_ROUTE } from './../../../../utils/consts';

const Security = () => {
  return (
    <>
      <LinkBack title='Security' path={ACCOUNT_ROUTE}/>
      <div className='security border-gradient'>
        <h1 className='security__title'>Security</h1>
        <div className='security__options'>
          <h3 className='options__title'>Login confirmation </h3>
          <div className='options__item'><input type="radio" name="myGroupName" value='dsad'/> <span>Single-phase authentication</span> </div>
          <div className='options__item'><input type="radio" name="myGroupName" value='d'/><span>Two-phase authentication</span> </div>
        </div>
        <h1 className='security__ip security__ip-main'>Ip address</h1>
        <p className='security__ip'>Logging in from an IP address now</p>
        <p className='security__ip security__ip-value'>91. 207.456.221</p>
      </div>
    </>
  );
};

export default Security;